@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.yellow{background-color:#F4C844}.blue{background-color:#80A3FF}.red{background-color:#FF8080}.yellow-text{color:#F4C844}.blue-text{color:#80A3FF}.red-text{color:#FF8080}.text-sm{font-size:calc(max(1vw, 12px))}.text-md{font-size:calc(min(max(5vw, 16px), 24px))}.text-lg{font-size:calc(min(max(5vw, 16px), 25px))}.text-xl{font-size:calc(min(max(10vw, 16px), 28px))}.h-fit{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.first-screen-content-inner{max-width:100%}.App{overflow-x:hidden;font-family:'IBM Plex Mono', monospace;font-weight:400;font-size:18px;margin:5vh 0 0 0}.fullscreen-outer{padding:0 20px}.fullscreen-inner{max-width:1000px;margin:auto}.bold{font-weight:700}.bolder{font-weight:bolder}.chrome-button-outer{border:2px black solid;border-radius:20px;height:100px;width:400px;max-width:100%;min-width:290px;box-shadow:5px 5px 5px 0px rgba(0,0,0,0.3)}.chrome-button-outer .chromestore-icon{max-height:50px;margin:auto;padding:0 0 0 5px}.header .ameyo-logo{max-height:90px;padding:15px}.placeholder{max-width:100%;max-height:100%;box-shadow:5px 5px 5px 0px rgba(0,0,0,0.3);border-radius:5px}.wave{width:100vw;padding-top:50px;max-height:10vh}a{color:black !important;text-decoration:none !important}.underline{text-decoration:underline}

